export default {
	data() {
		return {
			windowHeight:'100%',
			fstime:'获取验证码',
			otime:60,
			butDis:false,
			sjvalue:1,
			yzvalue:1,
			xmimavalue:''
		};
	},
	onLoad() {
		var _me = this;
		
		uni.getSystemInfo({
			success: function(res) {
				_me.windowHeight = res.windowHeight + 'px';
			}
		});
		
	},
	methods:{
		onKeyInput:function(e){
			this.sjvalue = e.target.value;
		},
		yzmInput:function(e){
			this.yzmvalue = e.target.value;
		},
		xmimaInput:function(e){
			this.xmimavalue = e.target.value;
		},
		fsclick:function(){
			if(this.sjvalue.length !== 11){
				uni.showModal({
					title: '提示',
					content: '请输入正确手机号！',
					showCancel: false
				});
			}else{
				var howe = setInterval(() => {
					if(this.otime == 0){
						this.fstime = "获取验证码";
						this.otime = 60;
						this.butDis = false;
						clearInterval(howe);
					}else{
						this.butDis = true;
						this.fstime = this.otime + 's后重新发送';
						this.otime --;
					}
				},1000);
			}
		},
		ggmimaBtn:function(){
			var me = this;
			uni.getStorage({
				key: 'howeKey',
				success: function (res) {
					var sjvalue = res.data.shouji;
					
					if(me.sjvalue.length !== 11 || me.sjvalue !== sjvalue){
						uni.showModal({
							title: '提示',
							content: '请输入正确手机号！',
							showCancel: false
						});
					}else if(this.yzmvalue == ''){
						uni.showModal({
							title: '提示',
							content: '请输入验证码！',
							showCancel: false
						});
					}else if(me.mimavalue == ''){
						uni.showModal({
							title: '提示',
							content: '请输入新登录密码！',
							showCancel: false
						});
					}else{
						uni.setStorage({
							key: 'howeKey',
							data: {
								shouji: me.sjvalue,
								mima: me.xmimavalue
							},
							success: function () {
								uni.showModal({
									title: '恭喜',
									content: '更改密码成功！',
									showCancel: false,
									success: function (res) {
										if (res.confirm) {
											uni.redirectTo({
												url: 'login'
											});
										}
									}
								});
							}
						});
					};
				},
				fail:function(){
					uni.showModal({
						title: '提示',
						content: '暂无此用户！',
						showCancel: false
					});
				}
			});
		}
	}
}
